.note:before {
  content: url(https://dummyimage.com/20x20/000/fff);
}
.warning:before {
  content: "[注意]";
  color: #ff0000;
  background: #fff;
}
div.info {
  border: dotted 3px #ff9900;
  padding: 0.2em 1.3em;
}
q {
  quotes: "[" "]";
}
q:before {
  content: open-quote;
} 
q:after {
  content: close-quote;
}